import React from "react";
import { Field, reduxForm } from "redux-form";
import { Textarea } from "../utils/formControls";
import { maxLength, required } from "../utils/validator/validatorForm";
import Dialog from "./Dialog/Dialog";
import s from "./Dialogs.module.css";
import Message from "./Message/Message";
const maxLength60 = maxLength(60);
const MessageForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field
          placeholder={"new Message"}
          name={"newMessage"}
          component={Textarea}
          validate={[required, maxLength60]}
        />
      </div>

      <button>Add Message</button>
    </form>
  );
};

const ReduxFormMessage = reduxForm({ form: "message" })(MessageForm);

class Dialogs extends React.Component {
  constructor(props) {
    super(props);
  }

  dialogsElements() {
    return this.props.dialogsData.map((dialog) => {
      return <Dialog name={dialog.name} id={dialog.id} />;
    });
  }

  messageElements() {
    return this.props.messagesData.map((message) => {
      return <Message message={message.message} />;
    });
  }

  render() {
    let onSubmit = (formData) => {
      console.log(formData);
      this.props.AddMessage(formData.newMessage);
    };

    return (
      <div className={s.dialogs}>
        <div className={s.dialogsItems}>{this.dialogsElements()}</div>
        <div>
          <div className={s.messages}>{this.messageElements()}</div>
          <ReduxFormMessage onSubmit={onSubmit} />
        </div>
      </div>
    );
  }
}

export default Dialogs;
